/**,*::before,*::after{
	box-sizing: none !important;
}*/
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
table {
  display: inline-block;
  border-collapse: separate;
  border-spacing: 0px;
  margin-left: 25px;
}
.arrowPad {
  margin-top: 20px;
}
.arrowPad > tbody > tr > td {
  background: darkgrey;
  border-bottom: 2px solid black;
  border-right: 2px solid black;
  border-top: 2px solid lightgray;
  border-left: 2px solid lightgray;
  width: 40px;
  height: 40px;
  cursor: pointer;
}
.bomb.clicked {
  color: rgb(216, 0, 0) !important;
  font-size: 30px;
  padding: 0px;
}
.cell {
  width: 40px;
  height: 40px;
  font-size: 25px;
  color: white;
}
.clicked {
  background-color: grey;
  border: 4px solid darkgrey;
  -webkit-filter: brightness(80%);
          filter: brightness(80%);
}
.hidden {
  visibility: hidden;
}
.mapRow {
  height: 450px;
  text-align: center;
  margin: 0 auto
}
.menuDiv {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
}
p {
  margin-left: 31%;
}
.selected {
  border-bottom: 4px dotted red !important;
  border-right: 4px dotted red !important;
}
.selected .clicked {
  border: 2px dashed red !important;
}
.table {
  margin-left: 10px;
  position: relative;
}
.tableCol {
  margin: 0px;
  padding: 0px;
}
td {
  border-bottom: 4px solid grey;
  border-right: 4px solid grey;
  border-top: 4px solid rgb(234, 234, 234);
  border-left: 4px solid rgb(234, 234, 234);
}

/*cube.js*/
.cube {
  width: 100px;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
}
.cube div {
  position: absolute;
  width: 70px;
  height: 70px;
  background-color: rgb(219, 219, 219, .1);
  box-shadow: inset 0 0 30px rgba(125,125,125,0.8);
}
.wrap {
  width: 250px;
  -webkit-perspective: 800px;
          perspective: 800px;
  -webkit-perspective-origin: 25% 250px;
          perspective-origin: 25% 250px;
}





